@import "_partial/_config";

/* ################# About index ################# */
/* background */
.aboutHistory,
.aboutVoice,
.aboutStaff,
.aboutCast,
.aboutCharacter,
.aboutIndex {
	background: url(/img/img01.jpg) no-repeat center center;
	background-size: cover;
	background-attachment: fixed;
}
#overture {
	background: url(https://unsplash.it/g/1980/1501/?random) no-repeat center center;
	background-size: cover;
	background-attachment: fixed;
}
#point {
	background: url(https://unsplash.it/g/1980/1502/?random) no-repeat center center;
	background-size: cover;
	background-attachment: fixed;
}
#msgFromProducer {
	background: url(https://unsplash.it/g/1980/1503/?random) no-repeat center center;
	background-size: cover;
	background-attachment: fixed;
}

/* styles */
#aboutGear {
	padding-top: 16px;
}
#overture {
	.youtube {
		position: relative;
		padding-top: 56.309%;
		iframe {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
		}
	}
	.textBody {
		margin-top: 32px;
	}
	.telop {
		display: block;
		text-align: center;
	}
}
#point {
	.imgTxtBox {
		margin-top: 64px;
		@include imgTxtBox(50%, 50%);
		.imgBox {
			padding-right: 32px;
		}
		.txtBox {
			padding-left: 32px;
		}
		img {
			width: 100%;
			height: auto;
		}
		&:first-of-type {
			margin-top: 32px;
		}
	}
	.txtImgBox {
		margin-top: 64px;
		@include txtImgBox(50%, 50%);
		.imgBox {
			padding-left: 32px;
		}
		.txtBox {
			padding-right: 32px;
		}
		img {
			width: 100%;
			height: auto;
		}
	}
	.boxTitle {
		@include fontsize(28);
		font-weight: bold;
	}
	.boxText {
		margin-top: 16px;
	}
}
#msgFromProducer {
	.textBody {
		margin-top: 32px;
	}
	.producerProf {
		margin: 32px 0 0;
		padding: 16px;
		background: $color_bg_light;
		color: $color_text_dark;
	}
	.imgTxtBox {
		@include imgTxtBox(150px, 80%);
	}
	.producerProf_title {
		font-weight: normal;
		@include fontsize(24);
		.sp {
			display: none;
		}
	}
	.txtBox {
		padding-left: 16px;
	}
	.producerProf_body {
		margin-top: 8px;
	}
}
@media screen and (max-width: 768px) {
	.section {
		&Title {
			@include fontsize(24);
		}
	}
	#aboutGear {
		padding-top: 16px;
		.textBody {
			@include fontsize(14);
		}
	}
	#overture {
		.textBody {
			@include fontsize(14);
		}
	}
	#point {
		.txtImgBox,
		.imgTxtBox {
			display: block;
			.imgBox {
				width: auto;
				padding: 0;
			}
			.txtBox {
				width: auto;
				line-height: 1.6;
				padding: 0;
				@include fontsize(14);
			}
			.boxText {
				margin-top: 0;
			}
		}
	}
	#msgFromProducer {
		.producerProf {
			.imgBox {
				width: 75px;
				margin: 0 8px 8px 0;
				img {
					width: 100%;
				}
			}
			.txtBox {
				float: none;
				width: auto;
				line-height: 1.6;
				padding: 0;
				@include fontsize(14);
			}
			&_title {
				@include fontsize(18);
			}
			.sp {
				display: inline;
			}
		}
	}
}
@media screen and (max-width: 430px) {
	#overture {
		.telop {
			text-align: left;
		}
	}
}

/* ################# About character ################# */
#characters {
	padding-top: 16px;
	.imgTxtBox {
		margin-top: 64px;
		@include imgTxtBox(250px, 67%);
	}
	.txtImgBox {
		margin-top: 64px;
		@include txtImgBox(67%, 250px);
	}
	.txtBox_title {
		line-height: 1;
		@include fontsize(24);
	}
	.txtBox_body {
		line-height: 1.6;
		margin-top: 16px;
		@include fontsize(14);
	}
	.btn {
		margin-top: 8px;
		a {
			border: 1px solid rgba(255,255,255,.25);
		}
	}
	.profBox {
		margin: 32px 0 0;
		padding: 16px;
		background: $color_bg_light;
		color: $color_text_dark;
		.profBox_title {
			font-weight: normal;
			@include fontsize(24);
		}
		.profBox_body {
			margin-top: 8px;
			@include fontsize(14);
		}
	}
}
@media screen and (max-width: 768px) {
	#characters {
		.imgTxtBox,
		.txtImgBox {
			&:first-of-type {
				margin-top: 16px;
			}
			.imgBox,
			.txtBox {
				float: none;
				width: auto;
			}
			.imgBox {
				img {
					width: 100%;
				}
			}
			.txtBox {
				margin-top: 16px;
			}
			.btn {
				a {
					display: block;
					background: rgba(255, 255, 255, 0.25);
				}
			}
		}
	}
}

/* ################# About cast ################# */
#casts {
	padding-top: 16px;
	.categoryWrapper {
		position: relative;
	}
	.js-castGenre {
		position: absolute;
		top: 0;
		left: 0;
	}
	.genreNavi {
		margin-top: 16px;
		&_item {
			display: inline-block;
			width: 19.47%;
			text-align: center;
			padding: 0 0 0 4px;
			&:first-child {
				padding-left: 0;
			}
			&.active a {
				background: rgba(0,0,0,.75);
			}
		}
		a {
			display: block;
			padding: 8px 0;
			border: 2px solid $color_line;
			background: rgba(255,255,255,.75);
		}
	}
	.imgTxtBox {
		margin-top: 64px;
		@include imgTxtBox(48%, 48%);
		.imgBox {
			img {
				width: 100%;
			}
		}
	}
	.txtImgBox {
		margin-top: 64px;
		@include txtImgBox(48%, 48%);
		.imgBox {
			img {
				width: 100%;
			}
		}
	}
	.txtBox_name {
		@include fontsize(24);
	}
	.txtBox_job {
		margin-top: 8px;
		font-weight: normal;
	}
	.txtBox_body {
		margin-top: 8px;
	}
	.btn {
		margin-top: 8px;
		a {
			border: 1px solid #666;
			margin-top: 8px;
		}
	}
}

@media screen and (max-width: 768px) {
	#casts {
		.genreNavi {
			&_item {
				display: block;
				width: 50%;
				float: left;
				margin-top: 8px;
				&:nth-child(odd) {
					padding-left: 0;
				}
			}
		}
		.imgTxtBox,
		.txtImgBox {
			&:first-of-type {
				margin-top: 16px;
			}
			.imgBox,
			.txtBox {
				float: none;
				width: auto;
			}
			.imgBox {
				img {
					width: 100%;
				}
			}
			.txtBox {
				line-height: 1.6;
				margin-top: 16px;
				@include fontsize(14);
			}
			.btn {
				br {
					display: none;
				}
				a {
					display: block;
					background: rgba(255, 255, 255, 0.25);
				}
			}
		}
	}
}

/* ################# About staff ################# */
#staffs {
	padding-top: 16px;
	.staffList {
		text-align: center;
		.part {
			display: inline-block;
			margin-top: 32px;
			padding: 4px 16px;
			border-radius: 1000px;
			background: $color_bg_light;
			color: $color_text_dark;
		}
		.member {
			a {
				display: inline-block;
				margin: 16px 16px 0;
			}
		}
	}
}
#staffDetails{
	position: relative;
	.detailBox {
		position: absolute;
		top: 50%;
		left:50%;
		display: none;
		width: 75%;
		padding: 32px;
		background: $color_bg_light;
		color: $color_text_dark;
		transform: translate(-50%, -50%);
	}
	.imgTxtBox {
		@include imgTxtBox(48%, 48%);
		.imgBox {
			img {
				width: 100%;
			}
		}
	}
	.txtBox_name {
		@include fontsize(24);
	}
	.txtBox_job {
		margin-top: 8px;
		font-weight: normal;
	}
	.txtBox_body {
		margin-top: 8px;
	}
	.btn {
		margin-top: 8px;
		a {
			border: 1px solid #666;
			margin-top: 8px;
			color: $color_text_dark;
			&:hover {
				background: rgba(0,0,0,.75);
				color: $color_text;
			}
		}
		.close-staffDetails {
			display: none;
		}
	}
}
@media screen and (max-width: 768px){
	#staffDetails{
		.detailBox {
			top: 0;
			left:0;
			width: 100%;
			height: auto;
			padding: 10px;
			transform: translate(0,0);
		}
		.imgTxtBox {
			.imgBox {
				float: none;
				width: 100%;
			}
			.txtBox {
				float: none;
				width: 100%;
			}
		}
		.txtBox_name {
			margin-top: 8px;
		}
		.txtBox_body {
			line-height: 1.6;
			@include fontsize(14);
		}
		.btn {
			a {
				width: 100%;
			}
			.close-staffDetails {
				display: block;
			}
		}
	}
}

/* ################# About voice ################# */
#voices {
	padding-top: 16px;
	.navi {
		margin-top: 32px;
		.prev {
			float: left;
		}
		.next {
			float: right;
		}
		a {
			border: 1px solid $color_line;
		}
		.select {
			text-align: center;
		}
		.selectWrapper {
			position: relative;
			display: inline-block;
			line-height: 44px;
			border: 1px solid $color_line_dark;
			border-radius: 1000px;
			background: $color_bg_light;
			color: $color_text_dark;
			&:after {
				position: absolute;
				top: 0;
				right: 8px;
				z-index: 1;
				display: block;
				content: "\f078";
				font-family: FontAwesome;
			}
			select {
				position: relative;
				z-index: 2;
				padding: 0 30px;
			}
			option {
				text-align: left;
			}
		}
	}
	.voiceWrapper {
		margin-top: 32px;
		margin-left: -5%;
		.voiceBox {
			width: 28.333%;
			float: left;
			margin-top: 32px;
			margin-left: 5%;
			padding: 16px 0;
			border-top: 2px solid $color_line;
			text-align: center;
		}
		.voiceBody {
			@include fontsize(14);
		}
	}
	.tripadviser {
		margin: 32px 0;
		text-align: center;
		a {
			display: inline-block;
			position: relative;
			text-align: left;
			line-height: 1;
			padding: 8px 40px 8px 64px;
			border-radius: 1000px;
			@include fontsize(20);
			transition: all .4s ease;
			&:hover {
				background: $color_bg_light;
				color: $color_text_dark;
				&:after {
					border-color: $color_line_dark;
				}
			}
			&:before {
				position: absolute;
				top: 0;
				bottom: 0;
				left: 8px;
				margin: auto;
				width: 1em;
				height: 1em;
				line-height: 1;
				display: inline-block;
				content: "\f262";
				font-family: FontAwesome;
				@include fontsize(36);
			}
			&:after {
				position: absolute;
				top: 0;
				bottom: 0;
				right: 8px;
				margin: auto;
				display: inline-block;
				content: "\f054";
				border: 2px solid $color_line;
				text-align: center;
				@include fontsize(12);
				width: 1em;
				height: 1em;
				line-height: 1em;
				padding: 4px;
				box-sizing: content-box;
				border-radius: 1000px;
				font-family: FontAwesome;
			}
			.smallTxt {
				@include fontsize(12);
			}
		}
	}
}
@media screen and (max-width: 768px) {
	#voices {
		.navi {
			a {
				padding: 0 10px;
				@include fontsize(12);
			}
			.select {
				@include fontsize(12);
			}
		}
		.voiceWrapper {
			margin: 16px 0 0;
			.voiceBox {
				width: 100%;
				margin: 0;
			}
		}
	}
}

/* ################# About history ################# */
#histories {
	padding-top: 16px;
	.year {
		margin-top: 64px;
		.imgTxtBox {
			@include imgTxtBox(46%,50%);
		}
		.txtImgBox {
			@include txtImgBox(50%,46%);
		}
		.imgBox {
			img {
				width: 100%;
			}
		}
		.txtBox_name {
			@include fontsize(24);
			font-weight: normal;
		}
		.txtBox_title {
			margin-top: 16px;
		}
		.txtBox_body {
			@include fontsize(14);
		}
	}
}
@media screen and (max-width: 768px) {
	#histories {
		.year {
			margin-top: 32px;
			.txtImgBox,
			.imgTxtBox {
				.txtBox {
					float: none;
					width: 100%;
				}
				.imgBox {
					float: none;
					width: 100%;
					img {
						width: 100%;
					}
				}
			}
			.txtBox_name {
				margin-top: 16px;
				@include fontsize(24);
				font-weight: normal;
			}
			.txtBox_title {
				margin-top: 16px;
			}
			.txtBox_body {
				line-height: 1.6;
				@include fontsize(12);
			}
		}
	}
}